import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import { Box, Grid, } from "@mui/material"
import { useSelector } from 'react-redux';
import styled from '@emotion/styled';
import TableOrderSticker from '../../stickering/tableOrderSticker';
import "./tabs.css"

const LeftOfBack = ({ setMaintainMenuState, maintainMenuState, itemData }) => {

    const selecting2 = useSelector((state) => { return state.anotherAction })

    
    const RightBar = styled('div')({
        backgroundColor: '#317388',
        color: '#bad0d7',
        height: "90vh",
        // height: `${!trueing.stt ? '90vh' : '82vh'}`,
        // color:'#94d0ab'
        // color:'#b9e5e8'
        display: 'grid', 
        gridTemplateColumns: 'repeat(2, 1fr)',
        overflowY: 'auto',

    })

    const StickerBox = styled(Box)({
        backgroundColor: '#ffce66',
        margin: '20px',


        height: '220px',
        width: '90%',

        borderBottomRightRadius: '100px 19px',
        borderTopRightRadius: '100px 20px',
        borderTopLeftRadius: '5px 25px',
        borderBottomLeftRadius: '10px 30px',


        padding: '3px',
        border: '1px solid #fff',
        bosShadow: '0 0 10px #666',
        transition: '0.3s',
        '&:hover': {
            // cursor: 'pointer',
            borderRadius: '1px',
        }
    })



    const ContentBox = styled(Box)({
        // backgroundColor: 'red',
        color: '#317388',
        height: '100%',
        // border:'solid',
    })


    return (
        <Grid item xs={8}>

            <div>

                <Tabs onSelect={(pp) => { setMaintainMenuState(pp); sessionStorage.setItem('keyForMenu', pp) }} selectedIndex={maintainMenuState}>
                    <TabList >
                        {itemData.map((d, k) => {

                            return <Tab key={k} >{`${d.name}`}</Tab>
                        })}
                    </TabList>
                    {
                        itemData.map((indexNum, indexKey) => {
                            // console.log(indexNum);
                            // console.log(indexNum);
                            // console.log(sessionStorage.getItem('keyForMenu'));
                            // console.log(maintainMenuState);
                            return (

                                <TabPanel key={indexKey}>

                                    <RightBar>
                                        {itemData[maintainMenuState].ItemNames.map((d, k) => (
                                            <StickerBox key={k}>
                                                <ContentBox>
                                                    <TableOrderSticker names={d} prices={itemData[maintainMenuState].ItemPrices[k]} orderOf={selecting2.specificOrderName.tablefor} />

                                                </ContentBox>
                                            </StickerBox>
                                        ))}

                                    </RightBar>
                                </TabPanel>

                            )
                        })
                    }


                </Tabs>

            </div>
            <div style={{}}>

            </div>
        </Grid>
    )
}

export default LeftOfBack