import React from 'react'
import TableReservationInput from '../../siding/tableReservationInput'
import styled from '@emotion/styled'
import { Grid,  } from "@mui/material"

const LeftOfFront = () => {
    const LeftBar = styled('div')({
        paddingTop: '40px',
        backgroundColor: '#e3ecef',
        // height: '93vh',
        color: 'grey',
        fontFamily: "cursive",
        // color: '#bad0d7',
        height: '100%',
        textAlign: 'center',
        // border: 'solid'
    })
    return (
        <Grid item xs={4}>
            <LeftBar>
                <h1>Table Reservation</h1>

                <br />
                <TableReservationInput />
            </LeftBar>
        </Grid>
    )
}

export default LeftOfFront