import styled from "@emotion/styled";
import { Box, Button, CardActions, CardContent, Typography } from "@mui/material";
import EditIcon from '@mui/icons-material/Edit';
import { useDispatch, useSelector } from "react-redux";
import DeleteIcon from '@mui/icons-material/Delete';
import { deleteReservation } from "../reduxForORS/slices";
import { addSpecificOrderName } from "../reduxForORS/slices2";

const TableReservationSticker = (props) => {

    const reservations = useSelector((state) => { return state.someAction })

    const dispatching = useDispatch()

    const CardContenting = styled(Box)({
        display: 'flex',
        textAlign: 'center',
        // backgroundColor:'red',
        fontFamily: 'cursive',
        marginTop: '11px',
        marginLeft: 'auto',
        marginRight: 'auto',
        width: '80%',
        // border:'solid'
    })

    const Typographing = styled(Typography)({

        fontFamily: 'cursive',
        marginLeft: '10px'
    })

    const Thumbpin = styled(Box)({
        // backgroundColor: 'gray',

        background: " rgb(255,255,255)",
        background: "radial-gradient(circle, rgba(255,255,255,1) -40%, rgba(255,255,255,1) 0%, rgba(128,128,128,1) 87%, rgba(128,128,128,1) 94%)",

        height: '30px',
        width: '30px',
        borderRadius: '100px',
        margin: '0',
        padding: '0',
    })

    const ContainerHover = styled('div')({
        marginTop: '-10px',


        '&:hover': {
            // cursor:'wait',
            '& .editBeast': {
                opacity: '1'
            }
        }
    })
    const EditBeast = styled('div')({
        // border:'solid',
        width: '30px',
        // height: '30px',
        marginLeft: 'auto',
        marginRight: '10px',
        opacity: '0',

        // padding:'10px'

        '&:hover': {
            cursor: 'pointer'
        }
    })
    // console.log(props);
    return (
        <>
            <Thumbpin />
            <ContainerHover>
                <CardContenting  >
                    <Typographing sx={{ fontWeight: 'bolder', fontSize: '18px' }}>Table No.: </Typographing>
                    <Typographing sx={{ fontWeight: 'bolder', fontSize: '18px' }}>{props.childKey + 1}</Typographing>
                </CardContenting>
                <CardContenting  >
                    <Typographing sx={{ fontWeight: 'bolder', fontSize: '18px' }}>For: </Typographing>
                    <Typographing sx={{ fontWeight: 'bolder', fontSize: '18px' }}>{props.d.tablefor}</Typographing>
                </CardContenting>
                <CardContenting>
                    <Typographing>Date: </Typographing>
                    <Typographing > {props.d.date}</Typographing>
                </CardContenting>

                <CardContenting>
                    <Typographing>Timing: </Typographing>
                    <Typographing   > {props.d.timings}</Typographing>
                </CardContenting>
                <CardContenting>
                    <Typographing>Sitting: </Typographing>
                    <Typographing   > {props.d.people}</Typographing>
                </CardContenting >
                <div style={{ display: 'flex', width: '75px', height: '50px', marginLeft: 'auto', marginTop: '-35px' }}>
                    <EditBeast className="editBeast" onClick={() => { dispatching(addSpecificOrderName({ ...props.d, ...{ 'tableNum': props.childKey + 1 } })); props.setTrueing({ stt: true, pl: 'horizontal' }); }}>
                        <EditIcon sx={{ padding: '1px', marginTop: '10px', backgroundColor: '#317388', borderRadius: '5px', color: '#ffce66' }} />
                    </EditBeast>
                    <EditBeast className="editBeast" onClick={() => { dispatching(deleteReservation(props.childKey)) }}>
                        <DeleteIcon sx={{ padding: '1px', marginTop: '10px', backgroundColor: '#317388', borderRadius: '5px', color: '#ffce66' }} />
                    </EditBeast>
                </div>
            </ContainerHover >

        </>
    )
}
export default TableReservationSticker;