
import { createSlice, original } from "@reduxjs/toolkit";
import { useSelector } from "react-redux";


export const theSlice2 = createSlice({
    name: 'second',
    initialState: {
        value: [],
        specificOrderName: [],
        orderedItems: [],
        waitingOrders: []
    },
    reducers: {
        orderList: (state, action) => {
            // console.log(state, action);
            state.value.push(action.payload)
        },
        delOrderList: (state, action) => {
            // console.log(action.payload);
            state.value.splice(action.payload, 1)
        },
        addSpecificOrderName: (state, action) => {
            // console.log(action);
            state.specificOrderName = action.payload
        },
        orderedItemsFunc: (state, action) => {

            // console.log(original(state.orderedItems));
            // console.log(original(state.orderedItems));
            state.orderedItems.push(action.payload)
        },
        OrderWaitingToCompelete: (state, action) => {
            state.waitingOrders.push(action.payload)
        }
    }
})
export const { orderList } = theSlice2.actions
export const { delOrderList } = theSlice2.actions
export const { addSpecificOrderName } = theSlice2.actions
export const { orderedItemsFunc } = theSlice2.actions
export const { OrderWaitingToCompelete } = theSlice2.actions

export default theSlice2.reducer
