import { useDispatch, useSelector } from "react-redux";
import cc from "./checkoutTable.module.css"
import { OrderWaitingToCompelete } from "../reduxForORS/slices2";
export default function CheckoutTable({ setTrueing }) {


  const dispatching = useDispatch()

  const selecting2 = useSelector((state) => { return state.anotherAction })
  // console.log(selecting2);


  // const tableNumber = selecting2.specificOrderName && selecting2.specificOrderName.tableNum - 1;  //decremented becuase i previously incremented to avoid table num 0
  // // console.log(tableNumber);

  const totalAmountOfItems = selecting2.orderedItems[selecting2.orderedItems.length - 1] && selecting2.orderedItems[selecting2.orderedItems.length - 1].total
  // console.log(totalAmountOfItems);

  const orderedItems = selecting2.orderedItems[selecting2.orderedItems.length - 1]
  // console.log(orderedItems);

  const checkoutData = { ...selecting2.specificOrderName, ...{ 'orderedItems': orderedItems }, ...{ 'total': totalAmountOfItems } }
  // console.log(checkoutData);
  // const totalTables=selecting2.orderedItems


  const handleWaitingOrders = () => {
    dispatching(OrderWaitingToCompelete(checkoutData))
    // console.log(checkoutData);
  }

  return (
    <div className={cc.checkoutcontainer}>
      <div className={cc.checkoutheader}>
        <button className={cc.backbtn} onClick={() => { setTrueing({ stt: false, pl: 'horizontal' }); }}>Order Table</button>
        <h2>Checkout</h2>
      </div>
      <div className={cc.checkoutbody}>
        <div className={cc.checkouttable}>
          <table>
            <thead>
              <tr>
                <th>Table No.</th>
                <th>Name</th>
                <th>Date</th>
                <th>Time</th>
                <th>Chairs</th>
                <th>Orders</th>
                <th>Price</th>
              </tr>
            </thead>
            <tbody>
              {[checkoutData].map((parentD, k) => {
                // console.log(parentD);


                return (
                  <tr key={k}>
                    <td>Table: {parentD.tableNum}</td>
                    <td>Mr. {parentD.tablefor}</td>
                    <td>{parentD.date}</td>
                    <td>{parentD.timings}</td>
                    <td>{parentD.people}</td>
                    <td>{parentD.orderedItems && (Object.keys(parentD.orderedItems)).map((d, kc) => {
                      // console.log(checkoutData.orderedItems[d].orderOf, parentD.tablefor);
                      if (checkoutData.orderedItems[d].orderOf === parentD.tablefor) {
                        return (
                          <p key={kc}>{checkoutData.orderedItems[d] && checkoutData.orderedItems[d].names} ({checkoutData.orderedItems[d].amount})</p>
                        )
                      }
                    })}</td>
                    <td>Rs. {parentD.total}</td>
                  </tr>
                )
              })}
            </tbody>
          </table>
        </div>
      </div>
      <div className={cc.checkoutfooter}>
        <button onClick={() => { setTrueing({ stt: true, pl: 'vertical' }); handleWaitingOrders(); }} className={cc.donebtn}>Done</button>
      </div>
    </div >

  );
}
