import { Button, MenuItem, TextField } from "@mui/material"
import classes from "./tableReservationInput.module.css"
import TableBarIcon from '@mui/icons-material/TableBar';
import { useDispatch, useSelector } from "react-redux";
import { addReservation } from "../reduxForORS/slices"

const TableReservationInput = () => {

  const dispatching = useDispatch()
  const selecting = useSelector((state) => state.someAction)
  const handleReservation = () => {
    const form = document.getElementById('reservationFormId')
    const formData = new FormData(form)

    let obj = {};
    // console.log(selecting.value.length + 1);

    for (const [key, value] of formData) {
      obj[key] = `${value}`
    }



    // console.log(selecting);
    // console.log(obj,obj.tableFor);

    if (obj.tablefor == undefined || obj.tablefor == '') {
      alert('field table for must be filled')
    }
    else {
      dispatching(addReservation(obj))
    }

  }


  const timings = [
    {
      value: '6',
      label: '6 AM',
    },
    {
      value: '7',
      label: '7 AM',
    },
    {
      value: '8',
      label: '8 AM',
    },
    {
      value: '9',
      label: '9 AM',
    },
    {
      value: '10',
      label: '10 AM',
    },
    {
      value: '11',
      label: '11 AM',
    },
    {
      value: '12',
      label: '12 PM',
    },
    {
      value: '2',
      label: '2 PM',
    },
    {
      value: '3',
      label: '3 PM',
    },
    {
      value: '4',
      label: '4 PM',
    },
    {
      value: '5',
      label: '5 PM',
    },
    {
      value: '6',
      label: '6 PM',
    },
    {
      value: '7',
      label: '7 PM',
    },
    {
      value: '8',
      label: '8 PM',
    },
    {
      value: '9',
      label: '9 PM',
    },
    {
      value: '10',
      label: '10 PM',
    },
  ];

  const people = [
    {
      value: '2',
      label: '2',
    },
    {
      value: '4',
      label: '4',
    },
    {
      value: '8',
      label: '8',
    },
    {
      value: '12',
      label: '12',
    }
  ]



  return (
    <form id="reservationFormId">
      <TextField
        name="tablefor"
        label="Table For: "
        id="filled-size-normal"
        // defaultValue="Normal"
        variant="filled"
        sx={{ width: '80%' }}
      />

      <br />
      <br />

      <div className={classes.dateDivClass}>
        <label className={classes.dateLabelClass}>Date</label>
        <div className={classes.dateDiv2Class}>
          <input className={classes.dateInputClass} type='date' name="date" />
        </div >
      </div >

      <br />
      <br />

      <TextField
        name="timings"
        id="filled-select-currency"
        select
        label="Timings"
        defaultValue="10"
        sx={{ width: '80%' }}
        variant="filled"
      >
        {timings.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>

      <br />
      <br />

      <TextField
        name="people"
        id="filled-select-currency"
        select
        label="People"
        defaultValue="2"
        variant="filled"
        sx={{ width: '80%' }}
      >
        {people.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>

      <br />
      <br />
      <br />
      <Button variant="contained" sx={{ backgroundColor: '#317388' }} onClick={handleReservation}>
        Book Now  <TableBarIcon sx={{ marginLeft: '10px' }} />
      </Button>
    </form>
  )
}
export default TableReservationInput