import styled from "@emotion/styled";
import { Box, Button, CardActions, CardContent, Container, TextField, Typography } from "@mui/material";
import { useRef } from "react";
import { useDispatch } from "react-redux";
import { orderList } from "../reduxForORS/slices2";

const TableOrderSticker = (props) => {
    const dispatching = useDispatch()

    const numRef = useRef()

    const handleOrder = () => {
        let obj = {}

        // console.log(props, numRef.current.value);
        // console.log(props);
        obj = { ...props, amount: numRef.current.value }
        // console.log(obj);

        dispatching(orderList(obj))
    }

    const Thumbpin = styled(Box)({
        // backgroundColor: 'gray',

        background: " rgb(255,255,255)",
        background: "radial-gradient(circle, rgba(255,255,255,1) -40%, rgba(255,255,255,1) 0%, rgba(128,128,128,1) 87%, rgba(128,128,128,1) 94%)",

        height: '30px',
        width: '30px',
        borderRadius: '100px',
        margin: '0',
        padding: '0',
    })

    const CardContenting = styled(Box)({
        display: 'flex',

        // backgroundColor:'red',
        fontFamily: 'cursive',
        marginTop: '11px',
        marginLeft: 'auto',
        marginRight: 'auto',
        width: '60%',
        textAlign: 'center',
        // border: 'solid'
        filter: 'url(#marker-shape)',
        backgroundColor: '#317388', color: '#ffce66', fontWeight: 'bold',
    })

    const Typographing = styled(Typography)({

        fontWeight: 'bold',
        fontFamily: 'cursive',
        fontSize: '18px',
        margin: 'auto',
        // border:'solid'
    })
    // console.log(props);
    return (
        <>
            <Thumbpin />
            <div style={{ marginTop: '-25px' }}>

                <CardContenting >
                    <input type='number' min={1} ref={numRef} defaultValue={1} style={{ margin: 'auto', borderRadius: '10px', width: '40px', padding: '4px', }} />
                </CardContenting>
                <CardContenting >
                    <Typographing >{props.names}</Typographing>
                </CardContenting>
                <CardContenting >
                    <Typographing >{props.prices}$</Typographing>
                </CardContenting>
                <CardContenting sx={{ '&:hover': { cursor: 'pointer' } }} onClick={handleOrder}>
                    <Typographing ><Button sx={{ color: '#ffce66' }}>ADD</Button></Typographing>
                </CardContenting>

                <svg version="1.1" className="svg-filters" style={{ display: "none" }}>
                    <defs>
                        <filter id="marker-shape">
                            <feTurbulence type="fractalNoise" baseFrequency="0 0.02" numOctaves="1" result="warp" />
                            <feDisplacementMap xChannelSelector="R" yChannelSelector="G" scale="30" in="SourceGraphic" in2="warp" />
                        </filter>
                    </defs>
                </svg>
            </div>
        </>
    )
}
export default TableOrderSticker;