import styled from '@emotion/styled'
import { Box, Grid, } from "@mui/material"
import TableReservationSticker from '../../stickering/tableReservationSticker'
import { useSelector } from 'react-redux'

const RightOfFront = ({setTrueing}) => {

    const selecting = useSelector((state) => { return state.someAction })


    const RightBar = styled('div')({
        backgroundColor: '#317388',
        color: '#bad0d7',
        height: "90vh",
        // height: `${!trueing.stt ? '90vh' : '82vh'}`,
        // color:'#94d0ab'
        // color:'#b9e5e8'
        display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)',
        overflowY: 'auto',

    })

    
    const StickerBox = styled(Box)({
        backgroundColor: '#ffce66',
        margin: '20px',


        height: '220px',
        width: '90%',

        borderBottomRightRadius: '100px 19px',
        borderTopRightRadius: '100px 20px',
        borderTopLeftRadius: '5px 25px',
        borderBottomLeftRadius: '10px 30px',


        padding: '3px',
        border: '1px solid #fff',
        bosShadow: '0 0 10px #666',
        transition: '0.3s',
        '&:hover': {
            // cursor: 'pointer',
            borderRadius: '1px',
        }
    })

    
    const ContentBox = styled(Box)({
        // backgroundColor: 'red',
        color: '#317388',
        height: '100%',
        // border:'solid',
    })

    return (
        <Grid item xs={8}>
            <RightBar>
                {
                    selecting.value.map((d, k) => {
                        return (
                            <StickerBox key={k}>
                                <ContentBox>
                                    <TableReservationSticker d={d} childKey={k} setTrueing={setTrueing} />
                                </ContentBox>
                            </StickerBox>
                        )
                    })
                }


            </RightBar>
        </Grid>
    )
}

export default RightOfFront