import styled from "@emotion/styled";
import { Grid, } from "@mui/material"
import { useState } from "react";

import ReactCardFlip from "react-card-flip";
import itemData from "./menu.json"
import FinalOutput from "./FinalOutput";
import RightOfFront from "./components/frontSide/RightOfFront";
import LeftOfFront from "./components/frontSide/LeftOfFront";

import LeftOfBack from "./components/backSide/LeftOfBack"
import RightOfBack from "./components/backSide/RightOfBack"
import CheckoutTable from "./components/CheckoutTable"

const ORSApp = () => {

    const [maintainMenuState, setMaintainMenuState] = useState(0)


    const [trueing, setTrueing] = useState({ stt: false, pl: '' })

    const MainGrid = styled(Grid)({
        borderRadius: '30px',
        // position: 'absolute',
        // border: 'solid ',
        margin: '20px auto ',
        // overflow:'hidden',
        height: '90vh',
        width: '80%',
        // height: '90%',
        // left: '0',
        // right: '0',
        // top: '0',
        border: 'solid 6px #317388',
        borderStyle: 'double',
        // bottom: '0',
    })

    const MainGridBack = styled(Grid)({
        borderRadius: '30px',
        // position: 'absolute',
        border: 'solid 6px #317388',
        borderStyle: 'double',
        margin: '20px auto ',
        height: '90vh',
        width: '80%',
        // left: '0',
        // right: '0',
        // top: '0',
        // bottom: '0',

    })

    return (
        <ReactCardFlip flipDirection={'horizontal'} isFlipped={trueing.stt} >

            <MainGrid key={"front"} container >
                {trueing.pl !== 'vertical' ?
                    <>
                        <LeftOfFront />
                        <RightOfFront setTrueing={setTrueing} />
                    </>
                    :
                    <>
                        {/* <Grid container sx={{ padding: '10px' }}>
                            <FinalOutput />
                        </Grid>
                        <button onClick={() => { setTrueing({ stt: true, pl: 'vertical' }) }}>btn</button> */}
                        <div style={{
                            width: '100%', 
                            // border: 'solid 6px gray',
                            // borderStyle:'double',
                            borderRadius: '30px',
                            backgroundColor:'#317388'
                        }}>

                            <CheckoutTable setTrueing={setTrueing} />

                        </div>
                    </>
                }
            </MainGrid>

            <MainGridBack key={"back"} container >
                <LeftOfBack itemData={itemData} maintainMenuState={maintainMenuState} setMaintainMenuState={setMaintainMenuState} />
                <RightOfBack setTrueing={setTrueing} itemData={itemData} />
            </MainGridBack>


        </ReactCardFlip >
    )
}
export default ORSApp;