import { Button, MenuItem, TextField, Box, List, ListItem, ListItemAvatar, Avatar, ListItemText, IconButton, Grid, Typography } from "@mui/material"
import classes from "./tableOrderOutput.module.scss"
import RestaurantIcon from '@mui/icons-material/Restaurant';
import { useSelector, useDispatch } from "react-redux";
import { useEffect, useState } from "react";
import DeleteIcon from '@mui/icons-material/Delete';
import styled from "@emotion/styled";
import OrderList from "./orderList";
import { delOrderList, orderedItemsFunc, setOrderedItemListNull } from "../reduxForORS/slices2";

const TableOrderOutput = ({ setTrueing, tableNum }) => {

  const dispatching = useDispatch()

  const selecting = useSelector((state) => { return state.anotherAction })
  let adding = [0]
  let total;


  // console.log(selecting.specificOrderName);
  selecting.value.map((ddd, kkk) => {
    if (selecting.value[kkk] && selecting.specificOrderName.tablefor == selecting.value[kkk].orderOf) {
      // console.log(ddd.prices);
      adding.push(ddd.prices * ddd.amount)
      total = eval(adding.join('+'))
    }
  })

  const Demo = styled('div')(({ theme }) => ({
    backgroundColor: 'white',
  }));

  const ParentGrid = styled(Grid)({

    marginBottom: '10px',

    '&:hover ': {
      // transition:'1s',


      '& .delGrid': {
        display: 'block',
        background: '#e3ecef',

        '&:hover': {
          cursor: 'pointer',

        }
      },
    }
  })

  const afunc = () => {
    // console.log(selecting.orderedItems.length);
  }
  // console.log(selecting);
  // console.log(selecting.specificOrderName.tablefor,selecting.value);
  return (
    <>
      <div style={{ height: '45vh', overflowY: 'auto', backgroundColor: 'rgba(0, 0, 0, 0.06)', width: '90%', margin: 'auto', borderRadius: '10px', padding: '10px' }}>
        <ParentGrid container direction="row" >
          <Grid item xs >Names</Grid>
          <Grid item xs >Prices</Grid>
          <Grid className="delGridHeader" sx={{ display: 'none' }} item xs ><DeleteIcon /></Grid>
        </ParentGrid>
        <hr />
        <br />
        {selecting.value.map((d, k) => {
          // console.log(d);
          if (selecting.specificOrderName.tablefor == d.orderOf)
            return (
              <ParentGrid container direction="row" sx={{ '&:hover': {} }} key={k}>
                <Grid item xs >{d.names}{`(${d.amount})`}</Grid>
                <Grid item xs >{d.prices * d.amount}</Grid>
                <Grid onClick={() => { dispatching(delOrderList(k)); }} className="delGrid" sx={{ display: 'none', }} item xs><DeleteIcon sx={{ marginTop: '10px' }} /></Grid>
              </ParentGrid>
            )
        })}
      </div>
      <br />
      <div className={classes.dateDiv2Class} style={{ border: 'solid 1px', borderRadius: '10px', padding: '4px', width: '70%' }}>
        <label className={classes.dateLabelClass}>TOTAL</label>
        <input readOnly={true} className={classes.dateInputClass} type='text' value={`${total ? total : 0} $`} />
        <Button variant="contained" sx={{ backgroundColor: '#317388', width: '400px' }} onClick={() => { afunc(); setTrueing({ stt: false, pl: 'vertical' }); (dispatching(orderedItemsFunc({ ...selecting.value, ...{ 'total': total }, 'orderOf': tableNum }))) }}>
          {/* <Button variant="contained" sx={{ backgroundColor: '#317388', width: '400px' }} onClick={() => { console.log(selecting.value); dispatching(orderedItemsFunc(selecting.value)) }}> */}
          Order Now <RestaurantIcon />
        </Button>

      </div >
    </>
  )
}
export default TableOrderOutput