import { Box, Grid, } from "@mui/material"
import FlipIcon from "@mui/icons-material/Flip"
import shadows from "@mui/material/styles/shadows";
import TableOrderOutput from "../../siding/tableOrderOutput";
import styled from "@emotion/styled";
import { useSelector } from "react-redux";

const RightOfBack = ({ setTrueing }) => {

    const LeftBar = styled('div')({
        paddingTop: '40px',
        backgroundColor: '#e3ecef',
        // height: '93vh',
        color: 'grey',
        fontFamily: "cursive",
        // color: '#bad0d7',
        height: '100%',
        textAlign: 'center',
        // border: 'solid'
    })

    const selecting2 = useSelector((state) => { return state.anotherAction })

    // console.log(selecting2);
    return (
        <Grid item xs={4}>
            <Box boxShadow={shadows[9]} sx={{
                position: 'absolute', right: '23%', top: '6%', borderRadius: '10px',
                transition: '0.6s',
                '&:hover': {
                    transform: 'rotateY(180deg)',
                    cursor: 'pointer',
                    // transform: 'rotate(180deg)',
                }
            }}>
                <FlipIcon onClick={() => { setTrueing({ stt: false, pl: 'horizontal' }); }} />
            </Box>

            <LeftBar>
                <br />
                <h1 style={{fontSize:'25px'}}>Table {selecting2.specificOrderName.tableNum}</h1>
                <h1 style={{fontSize:'20px'}}>  Mr.{selecting2.specificOrderName.tablefor}</h1>
                <br />
                <TableOrderOutput setTrueing={setTrueing} tableNum={selecting2.specificOrderName.tableNum} />
            </LeftBar>
        </Grid>
    )
}

export default RightOfBack